* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  overflow-x: hidden;
}

.container {
  width: 100%;
  height: 100vh;
}
.container header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  padding: 30px;
}
.container header .nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.container header .nav .ul {
  width: 50%;
  height: 100vh;
  position: absolute;
  z-index: 10;
  top: 0;
  right: 0;
  background-color: rgba(30, 32, 41, 0.664);
  display: flex;
  padding-top: 150px;
  flex-direction: column;
  padding-left: 50px;
  gap: 10px;
  backdrop-filter: blur(8px);
  transform: translateX(100%);
  transition: transform 500ms ease-in-out;
}
.container header .nav .ul li {
  list-style: none;
  padding-block: 10px;
}
.container header .nav .ul li a {
  text-decoration: none;
  color: white;
  font-size: 20px;
}
.container header .nav .burger {
  z-index: 11;
}
.container header .ul.home li:nth-child(1) {
  font-weight: bold;
  border-right: 5px solid white;
}
.container header .ul.destination li:nth-child(2) {
  font-weight: bold;
  border-right: 5px solid white;
}
.container header .ul.crew li:nth-child(3) {
  font-weight: bold;
  border-right: 5px solid white;
}
.container header .ul.technology li:nth-child(4) {
  font-weight: bold;
  border-right: 5px solid white;
}
.container header .nav.show ul {
  transform: translateX(0);
}
.container .home-section {
  background-image: url(./images/background-home-mobile.jpg);
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}
.container .home-section .home-content {
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding-inline: 50px;
  align-items: center;
  text-align: center;
}
.container .home-section .home-content .home-content-subtitle {
  font-size: 14px;
  color: #D0D6F9;
  letter-spacing: 5px;
  text-transform: uppercase;
}
.container .home-section .home-content .home-content-title {
  font-size: 80px;
  font-weight: 800;
  text-transform: uppercase;
}
.container .home-section .home-content .home-content-body {
  line-height: 30px;
  font-size: 16px;
  color: #D0D6F9;
}
.container .home-section .home-content .home-content-explore {
  width: 150px;
  height: 150px;
  background-color: white;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
  position: relative;
  text-transform: uppercase;
  z-index: 2;
  cursor: pointer;
  margin-top: 80px;
}
.container .home-section .home-content .home-content-explore a {
  color: black;
  text-decoration: none;
}
.container .home-section .home-content .home-content-explore:hover::before {
  scale: 1.7;
}
.container .home-section .home-content .home-content-explore::before {
  transition: scale 500ms ease-in-out;
  content: "";
  width: 150px;
  height: 150px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 100%;
  position: absolute;
  z-index: -1;
}
.container .destination-section {
  width: 100%;
  min-height: 100vh;
  background-image: url(./images/background-destination-mobile.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container .destination-section .destination-content {
  padding-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  text-align: center;
  padding-inline: 30px;
}
.container .destination-section .destination-content .destination-content-image {
  width: 200px;
}
.container .destination-section .destination-content .destination-content-image img {
  width: 100%;
}
.container .destination-section .destination-content .destination-content-subtitle {
  font-size: 18px;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 5px;
}
.container .destination-section .destination-content .destination-content-subtitle span {
  padding-right: 10px;
  color: rgba(255, 255, 255, 0.2);
}
.container .destination-section .destination-content .destination-content-navigation {
  text-transform: uppercase;
  display: flex;
  gap: 20px;
}
.container .destination-section .destination-content .destination-content-navigation .tab {
  padding: 10px;
  cursor: pointer;
}
.container .destination-section .destination-content .destination-content-navigation .tab.active {
  border-bottom: 2px solid white;
}
.container .destination-section .destination-content .destination-content-title {
  font-size: 40px;
  text-transform: uppercase;
  letter-spacing: 5px;
}
.container .destination-section .destination-content .destination-content-body {
  font-size: 16px;
  line-height: 30px;
  color: #D0D6F9;
}
.container .destination-section .destination-content .destination-content-info {
  border-top: 1px solid #383B4B;
  width: 100%;
  padding-block: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  text-transform: uppercase;
  padding-bottom: 50px;
}
.container .destination-section .destination-content .destination-content-info div {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.container .destination-section .destination-content .destination-content-info div p {
  font-size: 18px;
  letter-spacing: 5px;
  color: #D0D6F9;
}
.container .destination-section .destination-content .destination-content-info div h1 {
  font-size: 30px;
  font-weight: 400;
}
.container .crew-section {
  width: 100%;
  min-height: 100vh;
  background-image: url(./images/background-crew-mobile.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
  padding-top: 100px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  padding-inline: 50px;
  gap: 30px;
}
.container .crew-section .crew-subtitle {
  font-size: 20px;
  text-transform: uppercase;
  font-weight: 800;
}
.container .crew-section .crew-subtitle span {
  color: rgba(255, 255, 255, 0.25);
  padding-right: 10px;
}
.container .crew-section .crew-image {
  width: 80%;
  border-bottom: 1px solid #707070;
}
.container .crew-section .crew-image img {
  width: 60%;
}
.container .crew-section .crew-navigation {
  display: flex;
  gap: 15px;
}
.container .crew-section .crew-navigation .crew-tab {
  cursor: pointer;
  width: 14px;
  height: 14px;
  background-color: rgba(255, 255, 255, 0.418);
  border-radius: 100%;
}
.container .crew-section .crew-navigation .crew-tab.active {
  background-color: white;
}
.container .crew-section .crew-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 20px;
}
.container .crew-section .crew-content .crew-content-role {
  font-size: 18px;
  color: #a5a5a5;
  text-transform: uppercase;
}
.container .crew-section .crew-content .crew-content-name {
  font-size: 30px;
  text-transform: uppercase;
  font-weight: 400;
}
.container .crew-section .crew-content .crew-content-body {
  font-size: 16px;
  line-height: 30px;
  color: #D0D6F9;
}
.container .technology-section {
  width: 100%;
  min-height: 100vh;
  background-image: url(./images/background-technology-mobile.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
  padding-top: 100px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  gap: 30px;
}
.container .technology-section .technology-subtitle {
  font-size: 20px;
  text-transform: uppercase;
  font-weight: 800;
}
.container .technology-section .technology-subtitle span {
  color: rgba(255, 255, 255, 0.25);
  padding-right: 10px;
}
.container .technology-section .technology-image {
  width: 100%;
}
.container .technology-section .technology-image img {
  width: 100%;
}
.container .technology-section .technology-navigation {
  display: flex;
  gap: 15px;
}
.container .technology-section .technology-navigation .technology-tab {
  cursor: pointer;
  width: 50px;
  height: 50px;
  background-color: transparent;
  border: 1px solid white;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  user-select: none;
}
.container .technology-section .technology-navigation .technology-tab.active {
  color: black;
  background-color: white;
}
.container .technology-section .technology-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 20px;
  padding-inline: 50px;
}
.container .technology-section .technology-content .technology-content-subtitle {
  font-size: 18px;
  color: #a5a5a5;
  text-transform: uppercase;
}
.container .technology-section .technology-content .technology-content-name {
  font-size: 30px;
  text-transform: uppercase;
  font-weight: 400;
}
.container .technology-section .technology-content .technology-content-description {
  font-size: 16px;
  line-height: 30px;
  color: #D0D6F9;
}

@media screen and (min-width: 800px) {
  .container header {
    padding-left: 30px;
    padding-right: 0;
    padding-block: 0;
  }
  .container header .nav .ul {
    position: unset;
    top: 0;
    left: 0;
    width: max-content;
    height: max-content;
    padding-top: 0;
    padding-left: 0;
    transform: translateX(0);
    flex-direction: row;
    backdrop-filter: blur(8px);
    padding-inline: 30px;
    gap: 20px;
    background-color: rgba(66, 66, 66, 0.164);
  }
  .container header .nav .ul li {
    height: 100%;
    padding-block: 40px;
  }
  .container header .nav .ul.home li:nth-child(1) {
    font-weight: bold;
    border-right: 0;
    border-bottom: 2px solid white;
  }
  .container header .nav .ul.destination li:nth-child(2) {
    font-weight: bold;
    border-right: 0;
    border-bottom: 2px solid white;
  }
  .container header .nav .ul.crew li:nth-child(3) {
    font-weight: bold;
    border-right: 0;
    border-bottom: 2px solid white;
  }
  .container header .nav .ul.technology li:nth-child(4) {
    font-weight: bold;
    border-right: 0;
    border-bottom: 2px solid white;
  }
  .container header .nav .burger {
    display: none;
  }
  .container .home-section {
    background-image: url(./images/background-home-tablet.jpg);
  }
  .container .home-section .home-content {
    padding-top: 150px;
    padding-inline: 100px;
  }
  .container .home-section .home-content .home-content-title {
    font-size: 100px;
  }
  .container .home-section .home-content .home-content-body {
    font-size: 18px;
    padding-inline: 100px;
  }
  .container .home-section .home-content .home-content-explore {
    margin-bottom: 20px;
    width: 200px;
    height: 200px;
  }
  .container .home-section .home-content .home-content-explore::before {
    width: 200px;
    height: 200px;
  }
  .container .home-section .home-content .home-content-explore:hover::before {
    scale: 1.3;
  }
  .container .destination-section {
    background-image: url(./images/background-destination-tablet.jpg);
  }
  .container .destination-section .destination-content .destination-content-subtitle {
    width: 100%;
    text-align: start;
  }
  .container .destination-section .destination-content .destination-content-body {
    font-size: 18px;
    line-height: 40px;
    padding-inline: 40px;
  }
  .container .destination-section .destination-content .destination-content-title {
    font-size: 62px;
    font-weight: 400;
  }
  .container .destination-section .destination-content .destination-content-image {
    width: 35%;
  }
  .container .destination-section .destination-content .destination-content-info {
    padding-bottom: 0;
    flex-direction: row;
    gap: 50px;
  }
  .container .destination-section .destination-content .destination-content-info div p {
    font-size: 16px;
  }
  .container .destination-section .destination-content .destination-content-info div h1 {
    font-size: 30px;
  }
  .container .crew-section {
    background-image: url(./images/background-crew-tablet.jpg);
    padding-top: 100px;
    display: grid;
    place-items: center;
    grid-template-areas: "subtitle" "role" "name" "description" "navigation" "image";
  }
  .container .crew-section .crew-subtitle {
    grid-area: subtitle;
    width: 100%;
    text-align: start;
  }
  .container .crew-section .crew-image {
    grid-area: image;
  }
  .container .crew-section .crew-navigation {
    margin-top: -100px;
    grid-area: navigation;
  }
  .container .crew-section .crew-content .crew-content-role {
    grid-area: role;
    font-size: 22px;
  }
  .container .crew-section .crew-content .crew-content-name {
    grid-area: name;
    font-size: 40px;
  }
  .container .crew-section .crew-content .crew-content-body {
    grid-area: description;
    padding-inline: 100px;
    font-size: 18px;
  }
  .container .technology-section {
    background-image: url(./images/background-technology-tablet.jpg);
  }
  .container .technology-section .technology-subtitle {
    padding-top: 100px;
    width: 100%;
    text-align: start;
    padding-left: 30px;
  }
  .container .technology-section .technology-subtitle {
    margin-top: -50px;
    margin-bottom: 20px;
  }
  .container .technology-section .technology-content-description {
    margin-bottom: 60px;
    padding-inline: 120px;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    overflow: hidden;
  }
  .container header {
    padding: 30px 0 30px 100px;
  }
  .container header .nav hr {
    display: block;
  }
  .container header .nav .ul {
    border-radius: 5px 0 0 5px;
    background-color: rgba(255, 255, 255, 0.11);
    padding-left: 120px;
    gap: 50px;
    width: 70%;
  }
  .container .home-section {
    background-size: cover;
    background-image: url(./images/background-home-desktop.jpg);
  }
  .container .home-section .home-content {
    display: grid;
    width: 100%;
    grid-template-areas: "subtitle . explore" "title . explore" "body . explore";
    text-align: start;
    padding-inline: 100px;
    gap: 20px;
  }
  .container .home-section .home-content .home-content-subtitle {
    grid-area: subtitle;
  }
  .container .home-section .home-content .home-content-title {
    grid-area: title;
  }
  .container .home-section .home-content .home-content-body {
    grid-area: body;
    padding: 0;
    width: 500px;
  }
  .container .home-section .home-content .home-content-explore {
    grid-area: explore;
  }
  .container .home-section .home-content .home-content-explore:hover::before {
    scale: 2;
  }
  .container .destination-section {
    background-size: cover;
    background-image: url(./images/background-destination-desktop.jpg);
  }
  .container .destination-section .destination-content {
    display: grid;
    grid-template-areas: "subtitle . ." "image . navigation" "image . name" "image . body" "image . info";
    align-items: flex-start;
    text-align: start;
    gap: 50px;
    padding-inline: 150px;
  }
  .container .destination-section .destination-content .destination-content-subtitle {
    grid-area: subtitle;
    margin-top: 20px;
  }
  .container .destination-section .destination-content .destination-content-image {
    width: 100%;
    grid-area: image;
    margin-top: 20px;
  }
  .container .destination-section .destination-content .destination-content-navigation {
    grid-area: navigation;
  }
  .container .destination-section .destination-content .destination-content-info {
    justify-content: flex-start;
    grid-area: info;
  }
  .container .destination-section .destination-content .destination-content-body {
    padding: 0;
    grid-area: body;
  }
  .container .destination-section .destination-content .destination-content-title {
    grid-area: name;
  }
  .container .crew-section {
    background-size: cover;
    background-image: url(./images/background-crew-desktop.jpg);
    display: grid;
    padding-inline: 200px;
    grid-template-areas: "subtitle image" "role image" "name image" "body image" "navigation  image";
    align-items: flex-start;
    text-align: start;
    place-items: start;
    justify-content: flex-start;
    padding-top: 200px;
  }
  .container .crew-section .crew-subtitle {
    grid-area: subtitle;
  }
  .container .crew-section .crew-image {
    grid-area: image;
    width: 95%;
    height: 100%;
    border: none;
  }
  .container .crew-section .crew-image img {
    width: 100%;
    height: 75%;
  }
  .container .crew-section .crew-navigation {
    grid-area: navigation;
  }
  .container .crew-section .crew-content {
    align-items: flex-start;
    justify-content: flex-start;
  }
  .container .crew-section .crew-content .crew-content-body {
    grid-area: body;
    padding: 0;
    padding-right: 50px;
  }
  .container .crew-section .crew-content .crew-content-role {
    padding-top: 100px;
    grid-area: role;
  }
  .container .crew-section .crew-content .crew-content-name {
    grid-area: name;
  }
  .container .technology-section {
    background-size: cover;
    background-image: url(./images/background-technology-desktop.jpg);
    display: grid;
    padding-left: 200px;
    grid-template-areas: "subtitle subtitle image" "navigation title image" "navigation name image" "navigation description image" ". body image" ". body image";
    place-items: start;
    justify-content: center;
    text-align: start;
    padding-top: 100px;
    align-items: center;
  }
  .container .technology-section .technology-image {
    width: 100%;
    grid-area: image;
  }
  .container .technology-section .technology-image img {
    width: 100%;
  }
  .container .technology-section .technology-subtitle {
    grid-area: subtitle;
    margin-top: 20px;
  }
  .container .technology-section .technology-navigation {
    grid-area: navigation;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: start;
    margin-top: -100px;
  }
  .container .technology-section .technology-content {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: flex-start;
  }
  .container .technology-section .technology-content .technology-content-subtitle {
    grid-area: title;
  }
  .container .technology-section .technology-content .technology-content-description {
    grid-area: description;
    margin: 0;
    padding: 0;
  }
  .container .technology-section .technology-content .technology-content-name {
    grid-area: name;
    font-size: 50px;
  }
}